import { cx } from '@emotion/css'
import mergeWith from 'lodash-es/mergeWith'

/**
 * A simplified version of "class-names" utility
 */

export type RulesArray = Array<string | undefined>
export type RulesRecord = Record<string, any>
export type Rule = string | undefined | null | false

export type CnClassNamesArgs = Array<RulesArray | RulesRecord | Rule>

export function cn (...rules: CnClassNamesArgs): string {
  return rules.map(rule => {
    if (Array.isArray(rule)) {
      return cnArray(rule)
    }

    if ((rule !== null) && (typeof rule === 'object')) {
      return cnRecord(rule)
    }

    return cnMaybeString(rule)
  }).join(' ').trim().replace(/\s{2}/g, ' ')
}

export const cnx = (...rules: CnClassNamesArgs): string => cx(cn(...rules))

function cnArray (rules: RulesArray): string {
  return rules.filter(Boolean).join(' ').trim()
}

function cnRecord (rules: RulesRecord): string {
  return Object
    .entries(rules)
    .reduce((acc, [className, value]): string => {
      const isEnabled = Boolean(value)
      return isEnabled ? [acc, className].join(' ') : acc
    }, '')
    .trim()
}

function cnMaybeString (rule: Rule): string {
  return (typeof rule === 'string') ? rule : ''
}

/**
 * Merging of MUI classes objects
 **/

export type MuiClassesObjectType = Partial<Record<string, string>>
export type MergeMuiClassesArgumentType = MuiClassesObjectType[]

export function mergeMuiClasses (...classes: MergeMuiClassesArgumentType): MuiClassesObjectType {
  return classes.reduce((mergedClasses, classObject) =>
    mergeWith(mergedClasses, classObject, (objValue, srcValue) => {
      if (objValue == null || srcValue == null) {
        return
      }

      return `${objValue as string} ${srcValue as string}`
    }), {})
}
